@tailwind base;
@tailwind components;
@tailwind utilities;

@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans:wght@300&family=Josefin+Sans:wght@600&family=Open+Sans:ital,wght@0,300;0,500;0,800;1,300;1,500;1,800&family=Rock+Salt&display=swap);

:root {
  --red: #A62639;
  --pink: #DB324D;
  --dark-grey: #56494E;
  --light-grey: #56494E;
  --burgundy: #511C29;
  --white: white;
  --black:black;
  --light-yellow: #FFEE32;
  --dark-yelloe: #FFD100;


}

* {
  border: solid 0px;
}

html {
  height:100%;
} 

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  /*flex-direction: column; */
}

.about-me {
  font-family: 'Josefin Sans';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-dark {
  background:url("./assets/helen-beach-black-white-curl.jpg");
  height: 655px;
  width: 495px;
}

.hover\:bg-light:hover {
  background:url("./assets/helen-beach-colour-curl.jpg");
}

